import { Button, Checkbox, Dialog, DialogContent, DialogTitle, FormControlLabel, IconButton, TextField, Tooltip } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import React, { useEffect, useState } from 'react';
import DataTable from '../../Misc/DataTable';
import firebase from '../../Database/Firebase';
import Popup from '../../Misc/Popup';
import SettingsIcon from '@material-ui/icons/Settings';
    
{// todo Change here
}
export default function CargoProspects() {

    const filters = ['pic', 'sector', 'source', 'shipper', 'origin'];
    const [data, setData] = useState('');
    const [q, setQ] = useState('');
    const [openPopup, setOpenPopup] = useState(false);
    const [OpenSearch, setOpenSearch] = useState(false);
    const [Filters, setFilters] = useState('');
    const [checked, setChecked] = useState({
        pic: true,
        shipper: false,
        source: false,
        sector: false,
        origin: false
    });

    useEffect(() => {
        const selectedFilters = [];
        filters.forEach((filter) => {
            if (checked[filter])
            {
                selectedFilters.push(filter);
            }
            setFilters(selectedFilters);
        });
    }, [checked])

    const [username, setUsername] = useState('');
    const [f_name, setF_name] = useState('');

    const HandleCheckChange = (e) => {
        setChecked({...checked, [e.target.name]: e.target.checked});
    }

    async function getUsername(){
        localStorage.getItem('username') !== undefined ? setUsername(localStorage.getItem('username')) : setUsername(sessionStorage.getItem('username'));
        if(localStorage.getItem('username') == undefined && sessionStorage.getItem('username') == undefined)
        {
            window.location.href = '/';
        }
    }

    useEffect(() => {
        getUsername().then(() => {
            if(username)
            {
                firebase.database().ref('users').child(username).once('value').then(snapShot => {setF_name(snapShot.child('f-name').val())})
            }
        });
    }, [username])

    {// todo Change here
    }

    useEffect(() => {
        const path = firebase.database().ref('cargo').child('prospects');
        path.on('value', (snapShot) => {
            const list = [];
            const response = snapShot.val()
            for(let id in response)
            {
                list.push({id, ...response[id]});
            }
            setData(list);
        })
    }, []);

    const SearchData = (data) => {
        return data.filter((row) => 
            Filters.some(
                (filter) => row[filter].toString().toLowerCase().indexOf(q.toLowerCase()) > -1
            )
        );             
    }

    return (
        <div className='wrapper'>
            <div className="heading">
                {// todo Change here 
                }
                <h2>Cargo Prospects</h2>
            </div>
            <div className="ctrl-btns" style={{ width: '100%',display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around'}}>
                <Button color='primary' variant='contained' startIcon={<ArrowBackIosIcon />} onClick={() => {window.location.href ='/dashboard'}}  style={{margin: 20, height: 50}}>Back To Dashboard</Button>
                <Button color='primary' variant='contained' startIcon={<AddIcon />} style={{margin: 20, height: 50}} onClick={() => {setOpenPopup(true)}}>Add Cargo</Button>
                <div className="search" style={{borderRadius: 10, backgroundColor: 'white', width: 400, display: 'flex', flexWrap: 'wrap'}}>
                    <TextField label='Search'  style={{width: 300, marginLeft: 10, margin: 10}} onChange={(e) => {setQ(e.target.value)}}/>
                    <Tooltip title='Modify Search'><IconButton color='primary' onClick={() => {setOpenSearch(true)}}><SettingsIcon /></IconButton></Tooltip> 
                </div>
                {// todo Change here
                }
                <Popup initPath='cargo/prospects' title='Add Cargo' openPopup={openPopup} setOpenPopup={setOpenPopup} />
            </div>
            <div className="table-wrapper" style={{ overflow: 'auto', height: '85vh', width: '100%', marginTop: 20, borderRadius: 5}}>
            <table style={{minWidth: 300, color: 'grey', backgroundColor: 'white', cursor: 'default'}}>
            <thead>
                    <tr>
                        <th style={{textAlign: 'center', minWidth: 50,   margin: '25, 0'}}>Priority</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Sector</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Source</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Shipper</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Origin</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Destination</th>
                        <th style={{textAlign: 'center', minWidth: 300,   margin: '25, 0'}}>Cargo Particulars</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Lay Can</th>
                        <th style={{textAlign: 'center', minWidth: 200,   margin: '25, 0'}}>Frieght Indication</th>
                        <th style={{textAlign: 'center', minWidth: 500,   margin: '25, 0'}}>Commercial - Special Remarks</th>
                        <th style={{textAlign: 'center', minWidth: 500,   margin: '25, 0'}} colSpan='6'>Suitable Vessels</th>
                        <th style={{textAlign: 'center', minWidth: 500,   margin: '25, 0'}}>Comments and Follow ups</th>
                        <th style={{textAlign: 'center', minWidth: 500,   margin: '25, 0'}}>Business Conversation Status / Comments</th>
                        <th style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>PIC</th>
                        <th style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>Actions </th>
                    </tr>
                    <tr>
                        <th colSpan='10'></th>
                        <th  style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>Name</th>    
                        <th  style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>DWT</th>    
                        <th  style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>Open</th>    
                        <th  style={{textAlign: 'center', minWidth: 100,   margin: '25, 0'}}>Date</th>    
                        <th  style={{textAlign: 'center', minWidth: 200,   margin: '25, 0'}}>Hire Indication</th>    
                        <th  style={{textAlign: 'center', minWidth: 300,   margin: '25, 0'}}>Ops Special Remarks</th>  
                        <th colSpan='4'></th>  
                    </tr>    
                </thead> 
                <tbody>
                    {
                        data ? SearchData(data).map((row, index) => (
                            <>
                            {// todo Change here
                            }   
                            <DataTable data={row} id={row.id} tableName='cargo_prospects' initPath='cargo/prospects' />                            
                            <Popup openPopup={openPopup} setOpenPopup={setOpenPopup} tableName='cargo_prospects'   title='Add Cargo' key={index} initPath='cargo/prospects' username={f_name} />
                            </>
                        ))
                        : ''
                    }
                </tbody>
                </table>
            </div>
        { 
            // ! The search dialog starts here 👇👇
        }
        <Dialog open={OpenSearch} fullWidth={true} maxWidth='sm'>
            <DialogTitle style={{textAlign: 'center'}}>Search Filters</DialogTitle>
            <DialogContent>
                <div style={{display: 'flex', flexDirection: 'column'}}>
                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.pic}
                            onChange={HandleCheckChange}
                            name="pic"
                            color="primary"
                        />
                        }
                        label="PIC"
                    />

                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.source}
                            onChange={HandleCheckChange}
                            name="source"
                            color="primary"
                        />
                        }
                        label="Source"
                    />

                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.shipper}
                            onChange={HandleCheckChange}
                            name="shipper"
                            color="primary"
                        />
                        }
                        label="Shipper"
                    />

                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.sector}
                            onChange={HandleCheckChange}
                            name="sector"
                            color="primary"
                        />
                        }
                        label="Sector"
                    />

                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.origin}
                            onChange={HandleCheckChange}
                            name="origin"
                            color="primary"
                        />
                        }
                        label="Origin"
                    />
                    <Button color='primary' style={{marginTop: 20}} onClick={() => {setOpenSearch(false)}} >Done</Button>
                </div>
            </DialogContent>
        </Dialog>
        </div>
    )
}
