import React from 'react';
import './App.css';
import { HashRouter as Router, Switch, Route} from 'react-router-dom';
import Dashboard from './components/Dashboard/Dashboard';
import ErrorPage from './components/Error/ErrorPage';
import LoginPage from './components/Login Page/LoginPage';
import BerthGranite from './components/Vessels/Vessel Berth/Granite/BerthGranite';
import BerthNonGranite from './components/Vessels/Vessel Berth/Non Granite/BerthNonGranite';
import EnrouteGranite from './components/Vessels/Vessel Enroute/Granite/EnrouteGranite';
import EnrouteNonGranite from './components/Vessels/Vessel Enroute/Non Granite/EnrouteNonGranite';
import FixedGranite from './components/Vessels/Vessel Fixed/Granite/FixedGranite';
import FixedNonGranite from './components/Vessels/Vessel Fixed/Non Granite/FixedNonGranite';
import CargoProspects from './components/Cargo/Prospects/CargoProspects';
import CargoFixedUs from './components/Cargo/Fixed By us/CargoFixedUs';
import CargoFixedOthers from './components/Cargo/Fixed by others/CargoFixedOthers';
import BranchFollow from './components/Follow Ups/Branch/BranchFollow';
import HOFollow from './components/Follow Ups/HO/HOFollow';
import CompetitorsGranite from './components/Competitors/Granite/CompetitorsGranite';
import CompetitorsNonGranite from './components/Competitors/Non Granite/CompetitorsNonGranite';
import ArchiveHomePage from './components/Archive/Archive Home page/ArchiveHomePage';
import ArchiveVesselsGranite from './components/Archive/Vessel/Granite/ArchiveVesselsGranite';
import ArchiveVesselsNonGranite from './components/Archive/Vessel/NonGranite/ArchiveVesselsNonGranite';
import ArchiveCargo from './components/Archive/Cargo/ArchiveCargo';
import UsersTable from './components/Users/UsersTable';
import ArchiveBranch from './components/Archive/Followups/Branch/ArchiveBranch';
import ArchiveHO from './components/Archive/Followups/HO/ArchiveHO'

function App() {
  return (
    <Router>
      <div className="App">
        <Switch>
          <Route path='/' exact component={LoginPage}/>
          <Route path='/dashboard' component={Dashboard}/>
          <Route path='/berth-granite' component={BerthGranite}/>
          <Route path='/berth-non-granite' component={BerthNonGranite}/>
          <Route path='/enroute-granite' component={EnrouteGranite}/>
          <Route path='/enroute-non-granite' component={EnrouteNonGranite}/>
          <Route path='/fixed-granite' component={FixedGranite}/>
          <Route path='/fixed-non-granite' component={FixedNonGranite}/>
          <Route path='/cargo-prospects' component={CargoProspects}/>
          <Route path='/fixed-by-us' component={CargoFixedUs}/>
          <Route path='/fixed-by-others' component={CargoFixedOthers}/>
          <Route path='/branch-followups' component={BranchFollow}/>
          <Route path='/ho-followups' component={HOFollow}/>
          <Route path='/competitors-granite' component={CompetitorsGranite}/>
          <Route path='/competitors-non-granite' component={CompetitorsNonGranite}/>
          <Route path='/archives' component={ArchiveHomePage}/>
          <Route path='/archives-vessels-granite' component={ArchiveVesselsGranite}/>
          <Route path='/archives-vessels-non-granite' component={ArchiveVesselsNonGranite}/>
          <Route path='/archives-cargo' component={ArchiveCargo}/>
          <Route path='/archives-branch-followups' component={ArchiveBranch}/>
          <Route path='/archives-ho-followups' component={ArchiveHO}/>
          <Route path='/user-info' component={UsersTable}/>
          <Route component={ErrorPage}/>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
