import { Button, Dialog, DialogContent, DialogTitle, TextField } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import firebase from '../Database/Firebase';
import EditIcon from '@material-ui/icons/Edit';
import BackspaceIcon from '@material-ui/icons/Backspace';
import {Link} from 'react-router-dom';
import './Dashboard.css';
import Footer from '../Misc/Footer';

export default function Dashboard() {
    var username = '';

    const [f_name, setF_name] = useState('');
    const [newFname, setNewFname] = useState('');
    const [user_name, setUser_name] = useState('');
    const [newUsername, setNewUsername] = useState('');
    const [newPass, setNewPass] = useState('');
    const [password, setPassword] = useState('');
    const [conf_pass, setConf_pass] = useState('');
    const [powers, setPowers] = useState('');
    const [cargoVisible, setCargoVisible] = useState('');
    const [branchVisible, setBranchVisible] = useState('');
    const [hoVisible, setHoVisible] = useState('');
    const [listVisible, setListVisible] = useState('');
    const [competitorsVisible, setCompetitorsVisible] = useState('');
    const [openEdit, setOpenEdit] = useState(false);

    useEffect(() => {
        if(localStorage.getItem('username') == null)
        {
            username = sessionStorage.getItem('username');
        }
        else
        {
            username = localStorage.getItem('username');
        }

        if(username == null || username == '')
        {
            window.location.href = '/';
        }

        firebase.database().ref('users').child(username).on('value', (snapShot) => {
            setF_name(snapShot.child('f_name').val());
            setNewFname(snapShot.child('f_name').val());
            setUser_name(snapShot.child('username').val());
            setNewUsername(snapShot.child('username').val());
            setNewPass(snapShot.child('password').val());
            setPassword(snapShot.child('password').val());
            setConf_pass(snapShot.child('password').val())
            setPowers(snapShot.child('powers').val());
            setCargoVisible(snapShot.child('cargoVisible').val());
            setBranchVisible(snapShot.child('branchVisible').val());
            setHoVisible(snapShot.child('hoVisible').val());
            setListVisible(snapShot.child('listVisible').val());
            setCompetitorsVisible(snapShot.child('competitorsVisible').val());
        });

    }, [])

    const LogOut = () => {
        localStorage.removeItem('username');
        window.location.href='/';
    }

    const EditUserProfile = () => {
        if(password == conf_pass)
        {
            const path = firebase.database().ref('users').child(localStorage.getItem('username') ? localStorage.getItem('username') : sessionStorage.getItem('username'))
            path.set({
                username: newUsername,
                password: newPass,
                'f-name': newFname,
                powers: powers
            }).then(() => {
                setOpenEdit(false);
                alert('Credentials updated');
            })
        }
        else
        {
            alert('Passwords did not match');
            
        }
        
    }

    const reset = () => {
        setOpenEdit(false);
        setNewUsername(username);
        setNewFname(f_name);
        setNewPass(password);
        setConf_pass(password);
    }

    return (
        <>
        <div className='dash-wrapper'>
            <div className="dash-headings">
                <img src="./PMI Logo.jpg" alt=""/>
                
                <div className="dash-txt">
                    <h2>Daily Updates</h2> 
                    <h3>Welcome {f_name}</h3>
                </div>

                <img src="./ECL Logo.jpg" alt=""/>
                <div className="user-profiles">
                    <img src="./Profile Picture [JPG].jpg" alt=""/>
                    <Button color='primary' variant='contained' style={{marginTop: 10, width: 170, fontSize: '0.7rem'}} startIcon={<EditIcon />} onClick={() => {setOpenEdit(true)}}>Edit Profile</Button>
                    <Button color='primary' variant='contained' style={{marginTop: 10, width: 170, fontSize: '0.7rem'}} startIcon={<BackspaceIcon />} onClick={LogOut}>Logout</Button>
                </div>
            </div>
            <br/>
            <div className="table-list">
                <div className="top-row">
                    <div className="cargo-status" style={{visibility: powers !== 'Admin' && cargoVisible !== 'Visible' ? 'hidden' : 'visible'}}>
                        <h3>Cargo Status</h3>
                        <Link to='/cargo-prospects' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Prospects</Button></Link>
                        <Link to='/fixed-by-us' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Fixed By Us</Button></Link>
                        <Link to='/fixed-by-others' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Fixed by Others</Button></Link>
                    </div>
                    <div className="vessel-berth">
                        <h3>Vessels At Berth</h3>
                        <Link to='/berth-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Granite</Button></Link>
                        <Link to='/berth-non-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Non Granite</Button></Link>
                    </div>
                    <div className="vessel-enroute">
                        <h3>Vessels Enroute</h3>
                        <Link to='/enroute-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Granite</Button></Link>
                        <Link to='/enroute-non-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Non Granite</Button></Link>
                    </div>
                    <div className="vessel-fixed">
                        <h3>Vessels Fixed</h3>
                        <Link to='/fixed-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Granite</Button></Link>
                        <Link to='/fixed-non-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Non Granite</Button></Link>
                    </div>
                    <div className="vessel-list" style={{visibility: powers !== 'Admin' && listVisible !== 'Visible' ? 'hidden' : 'visible'}}>
                        <h3>Vessel List</h3>
                        <Link to='/list-eci' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>ECI</Button></Link>
                        <Link to='/list-wci' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>WCI</Button></Link>
                    </div> 
                   

                    <div className="competitors" style={{visibility: powers !== 'Admin' && competitorsVisible !== 'Visible' ? 'hidden' : 'visible',  width: 220}}>
                        <h3>Competitors Vessel</h3>
                        <Link to='/competitors-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Granite</Button></Link>
                        <Link to='/competitors-non-granite' style={{textDecoration: 'none'}} ><Button color='primary' variant='contained' style={{marginBottom: 10, width: 220, fontSize: '0.8rem'}}>Non Granite</Button></Link>
                    </div>  

                    

                </div>
                
                <div className="bottom-row">
                    <Link to='/archives' style={{textDecoration: 'none', marginTop: 10, visibility: powers !== 'Admin' ? 'hidden' : 'visible'}} ><Button color='primary' variant='contained' style={{marginRight: 50, width: 260, fontSize: '0.8rem'}}>Archive</Button></Link>
                    <Link to='/branch-followups' style={{textDecoration: 'none', marginTop: 10, visibility: powers !== 'Admin' && branchVisible !== 'Visible' ? 'hidden' : 'visible'}} ><Button color='primary' variant='contained' style={{marginRight: 50, width: 260, fontSize: '0.8rem'}}>Branch Follow Ups</Button></Link>
                    <Link to='/ho-followups' style={{textDecoration: 'none', marginTop: 10, visibility: powers !== 'Admin' && hoVisible !== 'Visible' ? 'hidden' : 'visible'}} ><Button color='primary' variant='contained' style={{marginRight: 50, width: 260, fontSize: '0.8rem'}}>HO Follow Ups</Button></Link>
                    <Link to='/user-info' style={{textDecoration: 'none', marginTop: 10, visibility: powers == 'Admin' ? 'visible' : 'hidden'}} ><Button color='primary' variant='contained' style={{marginRight: 50, width: 260, fontSize: '0.8rem'}}>User Info</Button></Link>
                </div>
               
            </div>
           
            <Footer />
        </div>

        <Dialog open={openEdit} onClose={() => {setOpenEdit(false)}} maxWidth='sm' fullWidth={true}>
            <DialogTitle>Edit Profile</DialogTitle>
            <DialogContent>
                <div className="inputs" style={{display: 'flex', flexWrap: 'wrap'}}>
                    <TextField label='Full Name' variant='outlined' style={{width: 400, margin: 20}} onChange={(e) => {setNewFname(e.target.value)}} value={newFname}/>
                    <TextField label='Username' variant='outlined' style={{width: 400, margin: 20}} onChange={(e) => {setNewUsername(e.target.value)}} value={newUsername} />
                    <TextField label='Password' variant='outlined' style={{width: 400, margin: 20}} onChange={(e) => {setNewPass(e.target.value)}} value={newPass} />
                    <TextField label='Confirm Password' variant='outlined' style={{width: 400, margin: 20}} onChange={(e) => {setConf_pass(e.target.value)}} value={conf_pass} />
                    <Button color='primary' variant='contained' style={{margin: 20, width: 150}} onClick={EditUserProfile}>Save</Button>
                    <Button color='secondary' variant='contained' onClick={reset} style={{margin: 20, width: 150}}>Cancel</Button>
                </div>
                
            </DialogContent>
        </Dialog>

        </>
    )
}
