import { Button, Checkbox, Dialog, DialogContent, DialogTitle, FormControlLabel, IconButton, TextField, Tooltip } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import React, { useEffect, useState } from 'react';
import DataTable from '../Misc/DataTable';
import firebase from '../Database/Firebase';
import Popup from '../Misc/Popup';
import SettingsIcon from '@material-ui/icons/Settings';

{// todo Change here
}
export default function UsersTable() {

    const filters = ['f_name', 'username'];
    const [data, setData] = useState('');
    const [q, setQ] = useState('');
    const [openPopup, setOpenPopup] = useState(false);
    const [OpenSearch, setOpenSearch] = useState(false);
    const [Filters, setFilters] = useState('');
    const [checked, setChecked] = useState({
        f_name: true,
        username: false
        
    });
    let display_data;

    useEffect(() => {
        const selectedFilters = [];
        filters.forEach((filter) => {
            if (checked[filter])
            {
                selectedFilters.push(filter);
            }
            setFilters(selectedFilters);
        });
    }, [checked])

    const [username, setUsername] = useState('');
    const [f_name, setF_name] = useState('');

    const HandleCheckChange = (e) => {
        setChecked({...checked, [e.target.name]: e.target.checked});
    }

    async function getUsername(){
        localStorage.getItem('username') !== undefined ? setUsername(localStorage.getItem('username')) : setUsername(sessionStorage.getItem('username'));
        if(localStorage.getItem('username') == undefined && sessionStorage.getItem('username') == undefined)
        {
            window.location.href = '/';
        }
    }

    useEffect(() => {
        getUsername().then(() => {
            if(username)
            {
                firebase.database().ref('users').child(username).once('value').then(snapShot => {setF_name(snapShot.child('f-name').val())})
            }
            
        });
    }, [username])

    {// todo Change here
    }

    useEffect(() => {
        const path = firebase.database().ref('users');
        path.on('value', (snapShot) => {
            const list = [];
            const response = snapShot.val()
            for(let id in response)
            {
                list.push({id, ...response[id]});
            }
            setData(list);
        })
    }, []);

    const SearchData = (data) => {
        display_data = data.filter((row) => 
            Filters.some(
                (filter) => row[filter].toString().toLowerCase().indexOf(q.toLowerCase()) > -1
            )
        );   
        return display_data;
    }

    return (
        <div className='wrapper'>
            <div className="heading">
                {// todo Change here 
                }
                <h2>Users Info</h2>
            </div>
            <div className="ctrl-btns" style={{ width: '100%',display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around'}}>
                <Button color='primary' variant='contained' startIcon={<ArrowBackIosIcon />} onClick={() => {window.location.href ='/dashboard'}}  style={{margin: 20, height: 50}}>Back To Dashboard</Button>
                <Button color='primary' variant='contained' startIcon={<AddIcon />} style={{margin: 20, height: 50}} onClick={() => {setOpenPopup(true)}}>Add User</Button>
                <div className="search" style={{borderRadius: 10, backgroundColor: 'white', width: 400, display: 'flex', flexWrap: 'wrap'}}>
                    <TextField label='Search'  style={{width: 300, marginLeft: 10, margin: 10}} onChange={(e) => {setQ(e.target.value)}}/>
                    <Tooltip title='Modify Search'><IconButton color='primary' onClick={() => {setOpenSearch(true)}}><SettingsIcon /></IconButton></Tooltip> 
                </div>
                {// todo Change here
                }
                <Popup initPath='users' title='Add User' openPopup={openPopup} setOpenPopup={setOpenPopup} />
            </div>
            <div className="table-wrapper" style={{ overflow: 'auto', height: '85vh', width: '100%', marginTop: 20, borderRadius: 5}}>
            <table style={{minWidth: 300, color: 'grey', backgroundColor: 'white', cursor: 'default'}}>
                <thead>
                    <tr>
                        <th style={{textAlign: 'center', minWidth: 200,   margin: '25, 0'}}>Full Name</th>
                        <th style={{textAlign: 'center', minWidth: 200,   margin: '25, 0'}}>Username</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Password</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Powers</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Cargo Status</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Branch Follow ups</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>HO Follow ups</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Vessel List</th>
                        <th style={{textAlign: 'center', minWidth: 150,   margin: '25, 0'}}>Competitors Vessel</th>
                        <th style={{textAlign: 'center', minWidth: 100,  paddingRight: 20}}>Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {
                        data ? SearchData(data).map((row, index) =>(
                            <>
                            {// todo Change here
                            }
                               
                            <DataTable data={row} id={row.id} tableName='users_info' initPath='users' />                            
                            <Popup openPopup={openPopup} setOpenPopup={setOpenPopup} tableName='users_info'   title='Add User' key={index} initPath='users' username={f_name} />
                            </>
                        ))
                        : ''
                    }
                </tbody>
                </table>
            </div>
        { 
            // ! The search dialog starts here 👇👇
        }
        <Dialog open={OpenSearch} fullWidth={true} maxWidth='sm'>
            <DialogTitle style={{textAlign: 'center'}}>Search Filters</DialogTitle>
            <DialogContent>
                <div style={{display: 'flex', flexDirection: 'column'}}>
                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.f_name}
                            onChange={HandleCheckChange}
                            name="f_name"
                            color="primary"
                        />
                        }
                        label="Full Name"
                    />


                    <FormControlLabel
                        control={
                        <Checkbox
                            checked={checked.username}
                            onChange={HandleCheckChange}
                            name="username"
                            color="primary"
                        />
                        }
                        label="Username"
                    />

                    <Button color='primary' style={{marginTop: 20}} onClick={() => {setOpenSearch(false)}} >Done</Button>
                </div>
            </DialogContent>
        </Dialog>
        </div>
    )
}
