import React, { useEffect, useState } from 'react';
import EditIcon from '@material-ui/icons/Edit';
import OpenWithIcon from '@material-ui/icons/OpenWith';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import { IconButton, makeStyles, Tooltip } from '@material-ui/core';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import firebase from '../Database/Firebase';
import Popup from './Popup';


export default function DataTable(props) {

  const {data, tableName, initPath, id} = props;

  const [openPopup, setOpenPopup] = useState(false);
  const [openDelete, setOpenDelete] = useState(false);
  const [openMove, setOpenMove] = useState(false);
  const [hasPowers, setHasPowers] = useState('');
  const [username, setUsername] = useState('');
  const [status, setStatus] = useState('')

  const useStyles = makeStyles({
    red:{
        backgroundColor:'#ff8080'
    },
    green:{
        backgroundColor:'#bbff99'
    },
    yellow:{
        backgroundColor:'#ffe680'
    }
});

    async function getUsername(){
        localStorage.getItem('username') !== null ? setUsername(localStorage.getItem('username')) : setUsername(sessionStorage.getItem('username'));
    }


    useEffect(() => {
        getUsername().then(() => {
            if(username)
            {
                firebase.database().ref('users').child(username).on('value', snapShot => {snapShot.child('f_name').val() == data.pic || snapShot.child('powers').val() == 'Admin' ? setHasPowers('visible') : setHasPowers('hidden')})
            }
        });
    }, [username])

    const classes = useStyles();

    const setFireState = (status) => {
        const path = firebase.database().ref(initPath).child(id).child('status');
        path.set(status);
    }



    if(tableName.includes('vessels'))
    {
        return(       
            <>
                <tr key={data.key}>
                    <td className={data.status == 'red' ? classes.red : data.status == 'yellow' ? classes.yellow : data.status == 'green' ? classes.green : ''}></td>
                    <td>{data.pic}</td>
                    <td>{data.vessel}</td>
                    <td>{data.dwt_crane}</td>
                    <td>{data.port}</td>
                    <td>{data.npc}</td>
                    <td>{data.hire}</td>
                    <td>{data.ac}</td>
                    <td>{data.imp_exp}</td>
                    <td>{data.cargo}</td>
                    <td>{data.arrived}</td>
                    <td>{data.berthed}</td>
                    <td>{data.total_qty}</td>
                    <td>{data.completed}</td>
                    <td>{data.balance}</td>
                    <td>{data.etcd}</td>
                    <td>{data.comments}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Vessel'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Vessel'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Vessel'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                    <Tooltip title='Status Red'><img src="./Red.png" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => setFireState('red')} /></Tooltip>
                                    <Tooltip title='Status Yellow'><img src="./yellow.jpeg" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => {setFireState('yellow')}} /></Tooltip>
                                    <Tooltip title='Status Green'><img src="./green2.png" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => {setFireState('green')}} /></Tooltip>
                                    <Tooltip title='Clear Status'><IconButton onClick={() => {setFireState('none')}}><HighlightOffIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Vessel' />
            </>
                    
        )
    }
    else if(tableName == 'cargo_FixedByUs' || tableName == 'cargo_prospects')
    {
        return(       
            <>
                <tr key={data.key}>
                    <td className={data.status == 'red' ? classes.red : data.status == 'yellow' ? classes.yellow : data.status == 'green' ? classes.green : ''}></td>
                    <td>{data.sector}</td>
                    <td>{data.source}</td>
                    <td>{data.shipper}</td>
                    <td>{data.origin}</td>
                    <td>{data.destination}</td>
                    <td>{data.cargo}</td>
                    <td>{data.lay_can}</td>
                    <td>{data.freight_indication}</td>
                    <td>{data.commercial}</td>
                    <td>{data.name}</td>
                    <td>{data.dwt}</td>
                    <td>{data.open}</td>
                    <td>{data.date}</td>
                    <td>{data.hire}</td>
                    <td>{data.opsRemarks}</td>
                    <td>{data.comments}</td>
                    <td>{data.business}</td>
                    <td>{data.pic}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Cargo'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Cargo'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Cargo'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                    <Tooltip title='Priority Red'><img src="./Red.png" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => setFireState('red')} /></Tooltip>
                                    <Tooltip title='Priority Yellow'><img src="./yellow.jpeg" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => {setFireState('yellow')}} /></Tooltip>
                                    <Tooltip title='Priority Green'><img src="./green2.png" alt="" style={{width: 30, height:30, borderRadius: 50, cursor: 'pointer', marginLeft: 5}} onClick={() => {setFireState('green')}} /></Tooltip>
                                    <Tooltip title='Clear Priority'><IconButton onClick={() => {setFireState('none')}}><HighlightOffIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Cargo'/>
            </>
                    
        )
    }
    else if(tableName == 'cargo_FixedByOthers')
    {
        return(       
            <>
                <tr key={data.key}>
                    <td>{data.sector}</td>
                    <td>{data.origin}</td>
                    <td>{data.destination}</td>
                    <td>{data.cargo}</td>
                    <td>{data.quantity}</td>
                    <td>{data.shipper}</td>
                    <td>{data.lay_can}</td>
                    <td>{data.fixedBy}</td>
                    <td>{data.name}</td>
                    <td>{data.frtRate}</td>
                    <td>{data.headsUp}</td>
                    <td>{data.offered}</td>
                    <td>{data.frtRateOffered}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Cargo'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Cargo'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Cargo'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Cargo'/>
            </>
                    
        )
    }
    else if(tableName.includes('followups'))
    {
        return(       
            <>
                <tr key={data.key}>
                    <td>{data.pic}</td>
                    <td>{data.task}</td>
                    <td>{data.startDate}</td>
                    <td>{data.targetDate}</td>
                    <td>{data.comments}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Task'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Task'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Task'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Task'/>
            </>
                    
        )
    }
    else if(tableName.includes('competitors'))
    {
        return(       
            <>
                <tr key={data.key}>
                    <td>{data.sector}</td>
                    <td>{data.origin}</td>
                    <td>{data.destination}</td>
                    <td>{data.cargo}</td>
                    <td>{data.quantityPlanned}</td>
                    <td>{data.shipper}</td>
                    <td>{data.line}</td>
                    <td>{data.open}</td>
                    <td>{data.name}</td>
                    <td>{data.arrived}</td>
                    <td>{data.berthed}</td>
                    <td>{data.etcd}</td>
                    <td>{data.finalQuantity}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Vessel'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Vessel'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Vessel'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Vessel' />
            </>
                    
        )
    }
    else if(tableName == 'archive_granite' || tableName == 'archive_nongranite')
    {
        return(       
            <>
                <tr key={data.key}>
                    <td>{data.pic}</td>
                    <td>{data.vessel}</td>
                    <td>{data.dwt_crane}</td>
                    <td>{data.port}</td>
                    <td>{data.npc}</td>
                    <td>{data.hire}</td>
                    <td>{data.ac}</td>
                    <td>{data.imp_exp}</td>
                    <td>{data.cargo}</td>
                    <td>{data.arrived}</td>
                    <td>{data.berthed}</td>
                    <td>{data.total_qty}</td>
                    <td>{data.completed}</td>
                    <td>{data.balance}</td>
                    <td>{data.etcd}</td>
                    <td>{data.comments}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit Vessel'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Move Vessel'><IconButton color='primary' onClick={() => {setOpenMove(true)}} ><OpenWithIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete Vessel'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Vessel' />
            </>
                    
        )
    }
    else if(tableName.includes('archive_cargo'))
    {

    }
    else
    {
        return(       
            <>
                <tr key={data.key}>
                    <td>{data.f_name}</td>
                    <td>{data.username}</td>
                    <td>{data.password}</td>
                    <td>{data.powers}</td>
                    <td>{data.cargoVisible}</td>
                    <td>{data.branchVisible}</td>
                    <td>{data.hoVisible}</td>
                    <td>{data.listVisible}</td>
                    <td>{data.competitorsVisible}</td>
                    <td>
                        {
                            <div style={{position: 'relative', overflow: 'hidden'}}>
                                <div className="txt-denied" style={{ visibility: hasPowers == 'visible' ? 'hidden' : 'visible', position: 'absolute', width:'100%', display: 'flex',  justifyContent:'center'}}>
                                    <h4>Access Denied</h4>
                                </div>
                                <div style={{width: '100%', height: '100%', alignItems: 'center', display: 'flex', visibility: hasPowers}}>
                                    <Tooltip title='Edit User'><IconButton color='primary' onClick={() => {setOpenPopup(true)}} ><EditIcon /></IconButton></Tooltip>
                                    <Tooltip title='Delete User'><IconButton color='secondary' onClick={() => {setOpenDelete(true)}} ><DeleteForeverIcon /></IconButton></Tooltip>
                                </div>
                            </div>
                        }
                    </td>
                </tr>
                <Popup tableName={tableName} data={data} id={data.id} openPopup={openPopup} setOpenPopup={setOpenPopup} openDelete={openDelete} setOpenDelete={setOpenDelete} openMove={openMove} setOpenMove={setOpenMove} initPath={initPath} username={username} status={status} title='Edit Vessel' />
            </>
                    
        )
    }
   
}
